  --text-muted: var(--gray-4);
  --text-light: var(--gray-5);
  --text-inverse: var(--gray-9);

  /* Border Colors */
  --border-color: var(--gray-7);
  --border-color-light: var(--gray-8);
  --border-color-dark: var(--gray-6);

