  border-color: transparent;
  color: var(--color-error);
}

.vd-btn-ghost-error:hover:not(:disabled):not(.disabled):not(.is-disabled) {
  background-color: var(--color-error-alpha-10);
  color: var(--color-error-dark);
}

.vd-btn-ghost-error:active:not(:disabled):not(.disabled):not(.is-disabled) {
  background-color: var(--color-error-alpha-20);
  color: var(--color-error-active);
}

.vd-btn-ghost-info {
  background-color: transparent;
  border-color: transparent;
  color: var(--color-info);
}

.vd-btn-ghost-info:hover:not(:disabled):not(.disabled):not(.is-disabled) {
  background-color: var(--color-info-alpha-10);
  color: var(--color-info-dark);
}

.vd-btn-ghost-info:active:not(:disabled):not(.disabled):not(.is-disabled) {
  background-color: var(--color-info-alpha-20);
  color: var(--color-info-active);
}

/* Ghost Button with Border (Subtle) */
.vd-btn-ghost-subtle {
  background-color: transparent;
  border: 1px solid var(--border-color);
  color: var(--text-secondary);
}

.vd-btn-ghost-subtle:hover:not(:disabled):not(.disabled):not(.is-disabled) {
  background-color: var(--gray-0);
  border-color: var(--gray-4);
  color: var(--text-primary);
}

.vd-btn-ghost-subtle:active:not(:disabled):not(.disabled):not(.is-disabled) {
  background-color: var(--gray-1);
  border-color: var(--gray-5);
}

/* Button Sizes - Small */
.vd-btn-sm {
  padding: var(--btn-padding-y-sm) var(--btn-padding-x-sm);
  font-size: var(--btn-font-size-sm);
  border-radius: var(--btn-border-radius-sm);
}

/* Button Sizes - Large */
.vd-btn-lg {
  padding: var(--btn-padding-y-lg) var(--btn-padding-x-lg);
  font-size: var(--btn-font-size-lg);
  border-radius: var(--btn-border-radius-lg);
}

/* Full Width Button */
.vd-btn-block,
.vd-btn-full {
  display: block;
  width: 100%;
}

/* Icon Button Support (Fibonacci squares) */
.vd-btn-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--btn-padding-y);
  width: auto;
  min-width: 2.125rem;
  /* 34px - fib */
  height: 2.125rem;
  /* 34px - fib */
}

.vd-btn-icon.vd-btn-sm {
  min-width: 2.125rem;
  /* 34px - fib */
  height: 2.125rem;
  /* 34px - fib */
  padding: var(--btn-padding-y-sm);
}

.vd-btn-icon.vd-btn-lg {
  min-width: 3.4375rem;
  /* 55px - fib */
  height: 3.4375rem;
  /* 55px - fib */
  padding: var(--btn-padding-y-lg);
}

/* Button with Icon Spacing */
.vd-btn>*+* {
  margin-left: 0.5rem;
}

.vd-btn>*:first-child {
  margin-left: 0;
}

/* Link as Button */
a.vd-btn {
  text-decoration: none;
}

a.vd-btn:hover {
  text-decoration: none;
}

/* Loading State */
.vd-btn.is-loading {
  position: relative;
  color: transparent;
  pointer-events: none;
}

.vd-btn.is-loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1em;
  height: 1em;
  margin: -0.5em 0 0 -0.5em;
  border: 2px solid currentColor;
  border-right-color: transparent;
  border-radius: 50%;
  animation: btn-spinner 0.6s linear infinite;
}

.vd-btn-primary.is-loading::after,
.vd-btn-secondary.is-loading::after,
.vd-btn-success.is-loading::after,
.vd-btn-error.is-loading::after,
.vd-btn-info.is-loading::after {
  border-color: var(--color-white);
  border-right-color: transparent;
}

.vd-btn-warning.is-loading::after {
  border-color: var(--color-black);
  border-right-color: transparent;
}

@keyframes btn-spinner {
  to {
    transform: rotate(360deg);
  }
}
/**
 * Vanduo Framework - Forms
 * Form components: inputs, textareas, labels, and validation
 */

:root {
  /* Input Padding (Fibonacci pairs: x/y ratio ~ phi)
   * sm:  8px/5px  = 1.6 ~ phi
   * base: 13px/8px = 1.625 ~ phi
   * lg:  21px/13px = 1.615 ~ phi */
  --input-padding-x-sm: 0.5rem;
  /* 8px  - fib */
  --input-padding-y-sm: 0.3125rem;
  /* 5px  - fib */
  --input-padding-x: 0.8125rem;
  /* 13px - fib */
  --input-padding-y: 0.5rem;
  /* 8px  - fib */
  --input-padding-x-lg: 1.3125rem;
  /* 21px - fib */
  --input-padding-y-lg: 0.8125rem;
  /* 13px - fib */

  /* Input Font Sizes */
  --input-font-size-sm: var(--font-size-sm);
  --input-font-size: var(--font-size-base);
  --input-font-size-lg: var(--font-size-lg);

  /* Input Border */
  --input-border-width: 1px;
  --input-border-color: var(--border-color);
  --input-border-radius: var(--btn-border-radius);
  --input-border-radius-sm: var(--btn-border-radius-sm);
  --input-border-radius-lg: var(--btn-border-radius-lg);

  /* Input Background */
  --input-bg: var(--color-white);
  --input-bg-disabled: var(--bg-secondary);

  /* Input Focus */
  --input-focus-border-color: var(--color-primary);
  --input-focus-box-shadow: 0 0 0 3px var(--color-primary-alpha-20);

  /* Input Transitions */
  --input-transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

/* Dark Theme Overrides */
[data-theme="dark"] {
  --input-bg: var(--bg-secondary);
  --input-bg-disabled: var(--bg-tertiary);
}

@media (prefers-color-scheme: dark) {
  :root:not([data-theme]) {
    --input-bg: var(--bg-secondary);
    --input-bg-disabled: var(--bg-tertiary);
  }
}

/* Base Input */
.vd-input,
input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="date"],
input[type="time"],
input[type="datetime-local"] {
  display: block;
  width: 100%;
  padding: var(--input-padding-y) var(--input-padding-x);
  font-family: var(--font-family-sans);
  font-size: var(--input-font-size);
  font-weight: var(--font-weight-normal);
  line-height: var(--line-height-normal);
  color: var(--text-primary);
  background-color: var(--input-bg);
  border: var(--input-border-width) solid var(--input-border-color);
  border-radius: var(--input-border-radius);
  transition: var(--input-transition);
  appearance: none;
}

.vd-input:focus,
input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="date"]:focus,
input[type="time"]:focus,
input[type="datetime-local"]:focus {
  outline: 0;
  border-color: var(--input-focus-border-color);
  box-shadow: var(--input-focus-box-shadow);
}

.vd-input::placeholder,
input::placeholder {
  color: var(--text-muted);
  opacity: 1;
}

.vd-input:disabled,
input:disabled,
.vd-input.disabled,
input.disabled {
  background-color: var(--input-bg-disabled);
  opacity: 0.6;
  cursor: not-allowed;
}

/* Textarea */
.textarea,
textarea {
  display: block;
  width: 100%;
  padding: var(--input-padding-y) var(--input-padding-x);
  font-family: var(--font-family-sans);
  font-size: var(--input-font-size);
  font-weight: var(--font-weight-normal);
  line-height: var(--line-height-normal);
  color: var(--text-primary);
  background-color: var(--input-bg);
  border: var(--input-border-width) solid var(--input-border-color);
  border-radius: var(--input-border-radius);
  transition: var(--input-transition);
  resize: vertical;
  min-height: 100px;
}

.textarea:focus,
textarea:focus {
  outline: 0;
  border-color: var(--input-focus-border-color);
  box-shadow: var(--input-focus-box-shadow);
}

.textarea::placeholder,
textarea::placeholder {
  color: var(--text-muted);
  opacity: 1;
}

.textarea:disabled,
textarea:disabled,
.textarea.disabled,
textarea.disabled {
  background-color: var(--input-bg-disabled);
  opacity: 0.6;
  cursor: not-allowed;
}

.textarea-no-resize,
textarea.no-resize {
  resize: none;
}

/* Input Sizes - Small */
.vd-input-sm,
input.vd-input-sm {
  padding: var(--input-padding-y-sm) var(--input-padding-x-sm);
  font-size: var(--input-font-size-sm);
  border-radius: var(--input-border-radius-sm);
}

.textarea-sm,
textarea.textarea-sm {
  padding: var(--input-padding-y-sm) var(--input-padding-x-sm);
  font-size: var(--input-font-size-sm);
  border-radius: var(--input-border-radius-sm);
}

/* Input Sizes - Large */
.vd-input-lg,
input.vd-input-lg {
  padding: var(--input-padding-y-lg) var(--input-padding-x-lg);
  font-size: var(--input-font-size-lg);
  border-radius: var(--input-border-radius-lg);
}

.textarea-lg,
textarea.textarea-lg {
  padding: var(--input-padding-y-lg) var(--input-padding-x-lg);
  font-size: var(--input-font-size-lg);
  border-radius: var(--input-border-radius-lg);
}

/* Input States - Error */
.vd-input-error,
input.vd-input-error,
.vd-input.is-error,
input.is-error {
  border-color: var(--color-error);
}

.vd-input-invalid,
input.vd-input-invalid {
  border-color: var(--color-error);
}

.vd-input-error:focus,
input.vd-input-error:focus,
.vd-input.is-error:focus,
input.is-error:focus {
  border-color: var(--color-error);
  box-shadow: 0 0 0 3px var(--color-error-alpha-10);
}

.vd-input-invalid:focus,
input.vd-input-invalid:focus {
  border-color: var(--color-error);
  box-shadow: 0 0 0 3px var(--color-error-alpha-10);
}

.textarea-error,
textarea.textarea-error,
.textarea.is-error,
textarea.is-error {
  border-color: var(--color-error);
}

.textarea-error:focus,
textarea.textarea-error:focus,
.textarea.is-error:focus,
textarea.is-error:focus {
  border-color: var(--color-error);
  box-shadow: 0 0 0 3px var(--color-error-alpha-10);
}

/* Input States - Success */
.vd-input-success,
input.vd-input-success,
.vd-input.is-success,
input.is-success {
  border-color: var(--color-success);
}

.vd-input-valid,
input.vd-input-valid {
  border-color: var(--color-success);
}

.vd-input-success:focus,
input.vd-input-success:focus,
.vd-input.is-success:focus,
input.is-success:focus {
  border-color: var(--color-success);
  box-shadow: 0 0 0 3px var(--color-success-alpha-10);
}

.vd-input-valid:focus,
input.vd-input-valid:focus {
  border-color: var(--color-success);
  box-shadow: 0 0 0 3px var(--color-success-alpha-10);
}

.textarea-success,
textarea.textarea-success,
.textarea.is-success,
textarea.is-success {
  border-color: var(--color-success);
}

.textarea-success:focus,
textarea.textarea-success:focus,
.textarea.is-success:focus,
textarea.is-success:focus {
  border-color: var(--color-success);
  box-shadow: 0 0 0 3px var(--color-success-alpha-10);
}

/* Labels */
.label,
label {
  display: inline-block;
  margin-bottom: 0.5rem;
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  color: var(--text-primary);
  line-height: var(--line-height-normal);
}

.label-required::after,
label.required::after {
  content: " *";
  color: var(--color-error);
}

.label-sm,
label.label-sm {
  font-size: var(--font-size-xs);
}

.label-lg,
label.label-lg {
  font-size: var(--font-size-base);
}

/* Form Groups */
.vd-form-group {
  margin-bottom: 1.3125rem;
  /* 21px - fib */
}

.vd-form-group:last-child {
  margin-bottom: 0;
}

/* Validation Messages */
.vd-form-error,
.error-message {
  display: block;
  margin-top: 0.5rem;
  font-size: var(--font-size-sm);
  color: var(--color-error);
}

.vd-form-feedback {
  display: block;
  margin-top: 0.5rem;
  font-size: var(--font-size-sm);
}

.vd-form-feedback-invalid {
  color: var(--color-error);
}

.vd-form-success,
.success-message {
  display: block;
  margin-top: 0.5rem;
  font-size: var(--font-size-sm);
  color: var(--color-success);
}

.vd-form-feedback-valid {
  color: var(--color-success);
}

.vd-form-help,
.help-text {
  display: block;
  margin-top: 0.5rem;
  font-size: var(--font-size-sm);
  color: var(--text-muted);
}

/* Input Groups (with prefix/suffix) */
.vd-input-group {
  display: flex;
  width: 100%;
  align-items: stretch;
}

.vd-input-group>.vd-input,
.vd-input-group>input {
  position: relative;
  flex: 1 1 auto;
  width: 1%;
  min-width: 0;
}

.vd-input-group>.vd-input:not(:first-child),
.vd-input-group>input:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: -1px;
}

.vd-input-group>.vd-input:not(:last-child),
.vd-input-group>input:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.vd-input-group-prefix,
.vd-input-group-suffix {
  display: flex;
  align-items: center;
  padding: var(--input-padding-y) var(--input-padding-x);
  font-size: var(--input-font-size);
  font-weight: var(--font-weight-normal);
  line-height: var(--line-height-normal);
  color: var(--text-secondary);
  text-align: center;
  white-space: nowrap;
  background-color: var(--bg-secondary);
  border: var(--input-border-width) solid var(--input-border-color);
}

.vd-input-group-prefix {
  border-right: 0;
  border-radius: var(--input-border-radius) 0 0 var(--input-border-radius);
}

.vd-input-group-suffix {
  border-left: 0;
  border-radius: 0 var(--input-border-radius) var(--input-border-radius) 0;
}

.vd-input-group>.vd-input-group-prefix+.vd-input,
.vd-input-group>.vd-input-group-prefix+input {
  border-left: 0;
}

.vd-input-group>.vd-input+.vd-input-group-suffix,
.vd-input-group>input+.vd-input-group-suffix {
  border-right: 0;
}

/* ============================================
   Phase 4: Custom Form Controls
   ============================================ */

/* Checkbox Variables */
:root {
  --checkbox-size: 1.3125rem;
  /* 21px - fib */
  --checkbox-size-sm: 0.8125rem;
  /* 13px - fib */
  --checkbox-size-lg: 2.125rem;
  /* 34px - fib */
  --checkbox-border-width: 2px;
  /* fib(2) */
  --checkbox-border-radius: var(--radius-fib-3);
  /* 3px */
  --checkbox-check-color: var(--color-white);
  --checkbox-bg-checked: var(--color-primary);
  --checkbox-bg-indeterminate: var(--color-primary);
  --checkbox-border-color: var(--input-border-color);
  --checkbox-transition: var(--transition-base);
}

/* Checkbox Wrapper */
.checkbox,
.vd-form-check {
  display: flex;
  align-items: center;
  margin-bottom: 0.8125rem;
  /* 13px - fib */
  position: relative;
  cursor: pointer;
  font-size: var(--input-font-size);
  line-height: var(--line-height-normal);
  color: var(--text-primary);
  user-select: none;
  gap: 0.5rem;
  /* 8px - fib */
}

/* Native checkbox styling (label.checkbox > input + span pattern) */
.checkbox>input[type="checkbox"],
.vd-form-check>input[type="checkbox"] {
  appearance: none;
  -webkit-appearance: none;
  width: var(--checkbox-size);
  height: var(--checkbox-size);
  border: var(--checkbox-border-width) solid var(--checkbox-border-color);
  border-radius: var(--checkbox-border-radius);
  background-color: var(--input-bg);
  cursor: pointer;
  flex-shrink: 0;
  margin: 0;
  position: relative;
  transition: var(--checkbox-transition);
}

.checkbox>input[type="checkbox"]:checked,
.vd-form-check>input[type="checkbox"]:checked {
  background-color: var(--checkbox-bg-checked);
  border-color: var(--checkbox-bg-checked);
}

.checkbox>input[type="checkbox"]:checked::after,
.vd-form-check>input[type="checkbox"]:checked::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 0.3rem;
  height: 0.6rem;
  border: solid var(--checkbox-check-color);
  border-width: 0 2px 2px 0;
}

.checkbox>input[type="checkbox"]:disabled,
.vd-form-check>input[type="checkbox"]:disabled {
  background-color: var(--input-bg-disabled);
  cursor: not-allowed;
  opacity: 0.6;
}

.checkbox>input[type="checkbox"]:disabled+span,
.vd-form-check>input[type="checkbox"]:disabled+span {
  opacity: 0.6;
  cursor: not-allowed;
}

.checkbox>input[type="checkbox"]:focus-visible,
.vd-form-check>input[type="checkbox"]:focus-visible {
  outline: 2px solid var(--input-focus-border-color);
  outline-offset: 2px;
}

.checkbox-inline,
.vd-form-check-inline {
  display: inline-flex;
  margin-right: 1.5rem;
  margin-bottom: 0;
  vertical-align: middle;
}

/* Checkbox Input (class-based pattern) */
.checkbox-input,
.vd-form-check-input {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
  margin: 0;
  cursor: pointer;
}

.checkbox-label,
.vd-form-check-label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  font-size: var(--input-font-size);
  line-height: var(--line-height-normal);
  color: var(--text-primary);
  user-select: none;
  padding-left: calc(var(--checkbox-size) + 0.5rem);
  position: relative;
  min-height: var(--checkbox-size);
}

.checkbox-label::before,
.vd-form-check-label::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: var(--checkbox-size);
  height: var(--checkbox-size);
  border: var(--checkbox-border-width) solid var(--checkbox-border-color);
  border-radius: var(--checkbox-border-radius);
  background-color: var(--input-bg);
  transition: var(--checkbox-transition);
  flex-shrink: 0;
}

.checkbox-input:checked+.checkbox-label::before,
.checkbox-input:checked~.checkbox-label::before,
.vd-form-check-input:checked+.vd-form-check-label::before {
  background-color: var(--checkbox-bg-checked);
  border-color: var(--checkbox-bg-checked);
}

.checkbox-input:checked+.checkbox-label::after,
.checkbox-input:checked~.checkbox-label::after,
.vd-form-check-input:checked+.vd-form-check-label::after {
  content: '';
  position: absolute;
  left: calc(var(--checkbox-size) / 2);
  top: 50%;
  transform: translate(-50%, -60%) rotate(45deg);
  width: 0.375rem;
  height: 0.75rem;
  border: solid var(--checkbox-check-color);
  border-width: 0 2px 2px 0;
  opacity: 1;
}

.checkbox-input:indeterminate+.checkbox-label::before,
.checkbox-input:indeterminate~.checkbox-label::before,
.vd-form-check-input:indeterminate+.vd-form-check-label::before {
  background-color: var(--checkbox-bg-indeterminate);
  border-color: var(--checkbox-bg-indeterminate);
}

.checkbox-input:indeterminate+.checkbox-label::after,
.checkbox-input:indeterminate~.checkbox-label::after,
.vd-form-check-input:indeterminate+.vd-form-check-label::after {
  content: '';
  position: absolute;
  left: calc(var(--checkbox-size) / 2);
  top: 50%;
  transform: translate(-50%, -50%);
  width: 0.5rem;
  height: 2px;
  background-color: var(--checkbox-check-color);
  border: none;
  opacity: 1;
}

.checkbox-input:focus-visible+.checkbox-label::before,
.checkbox-input:focus-visible~.checkbox-label::before,
.vd-form-check-input:focus-visible+.vd-form-check-label::before {
  outline: 2px solid var(--input-focus-border-color);
  outline-offset: 2px;
}

.checkbox-input:disabled+.checkbox-label,
.checkbox-input:disabled~.checkbox-label,
.vd-form-check-input:disabled+.vd-form-check-label {
  opacity: 0.6;
  cursor: not-allowed;
}

.checkbox-input:disabled+.checkbox-label::before,
.checkbox-input:disabled~.checkbox-label::before,
.vd-form-check-input:disabled+.vd-form-check-label::before {
  background-color: var(--input-bg-disabled);
  border-color: var(--checkbox-border-color);
  cursor: not-allowed;
}

/* Checkbox Sizes */
.checkbox-sm .checkbox-label,
.checkbox-sm .vd-form-check-label,
.vd-form-check-sm .checkbox-label,
.vd-form-check-sm .vd-form-check-label {
  font-size: var(--input-font-size-sm);
  padding-left: calc(var(--checkbox-size-sm) + 0.625rem);
  min-height: var(--checkbox-size-sm);
}

.checkbox-sm .checkbox-label::before,
.checkbox-sm .vd-form-check-label::before,
.vd-form-check-sm .checkbox-label::before,
.vd-form-check-sm .vd-form-check-label::before {
  width: var(--checkbox-size-sm);
  height: var(--checkbox-size-sm);
}

.checkbox-lg .checkbox-label,
.checkbox-lg .vd-form-check-label,
.vd-form-check-lg .checkbox-label,
.vd-form-check-lg .vd-form-check-label {
  font-size: var(--input-font-size-lg);
  padding-left: calc(var(--checkbox-size-lg) + 0.875rem);
  min-height: var(--checkbox-size-lg);
}

.checkbox-lg .checkbox-label::before,
.checkbox-lg .vd-form-check-label::before,
.vd-form-check-lg .checkbox-label::before,
.vd-form-check-lg .vd-form-check-label::before {
  width: var(--checkbox-size-lg);
  height: var(--checkbox-size-lg);
}

/* Checkbox Groups */
.checkbox-group,
.vd-form-check-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.checkbox-group-inline,
.vd-form-check-group-inline {
  flex-direction: row;
