/* Select (Enhanced Styling) */
select.vd-input,
select,
.select-input,
.custom-select-input {
  display: block;
  width: 100%;
  padding: var(--input-padding-y) var(--input-padding-x);
  padding-right: calc(var(--input-padding-x) + var(--select-arrow-size) + 0.5rem);
