  outline: 3px solid var(--color-primary);
  outline-offset: 2px;
}

/* Portrait orientation gallery item */
.vd-image-box-gallery .vd-image-box-trigger[data-orientation="portrait"] {
  aspect-ratio: 3 / 4;
}

/* Loading State */
.vd-image-box-trigger.is-loading {
  background: var(--bg-secondary);
